.movie__item {
  &:not(:last-child) {
    @media screen and (max-width: $to-tablet) {
      margin-bottom: 30px;
    }
  }

  @media screen and (min-width: $tablet) {
    flex-basis: calc((100% - 2 * 30px) / 2);
    margin-left: 30px;
    margin-top: 30px;
  }

  @media screen and (min-width: $desktop) {
    flex-basis: calc((100% - 90px) / 3);
    margin-left: 30px;
    margin-top: 30px;
  }
}
.movie__thumb {
  margin-bottom: 10px;
  max-width: 100%;
  height: auto;
}
.movie__thumb > img {
  display: block;
  height: 100%;
  width: 100%;

  object-fit: cover;
}
.movie__item > a {
  display: block;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 5px;
}

.movie__link:hover,
.movie__link:focus {
  box-shadow: 0 4px 4px 0 #757575;
}

.movie__text {
  font-weight: $font-weight;
  font-size: $font-size;
  line-height: $line-height;
  letter-spacing: 0.06em;
}
.movie__title {
  margin-bottom: 4px;
  color: $text-color;
}

.movie__genre {
  // margin-bottom: 20px;
  color: $accent-color;
}
